// http://meyerweb.com/eric/tools/css/reset/
// v2.0 | 20110126

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address,
big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center,
dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article,
aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

body ol, body ul, body ol.menu, body ul.menu {
  list-style: none;
  list-style-position:inside;
}

blockquote, q {
  quotes: none;
}

blockquote {
  &::before, &::after {
    content: '';
    content: none;
  }
}

q {
  &::before, &::after {
    content: '';
    content: none;
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*
 * Img Reset
 */
img{
	width:auto;
	height:auto;
    max-width:100%;
}

/*
 * Active Link Reset
 */
a.active{
	color:inherit
}

/*
 * Input Max Width - Keep them inside the screen in all circumstances
 */
input, select{
	max-width:99%;
	width:100%;
}
.SumoSelect .SlectBox{//slectbox is for sumo select
	width: 210px;
}
body .container-inline-date .form-item, body .container-inline-date .form-item input{//views datepicker reset
	max-width:99%;
	width:100%;
}
body .SumoSelect > .CaptionCont{
	border: 0;
	outline: 1px solid #a2a2a2;
}

/*
 * Responsive Tables - Focus On Full Support
 */
table{
  overflow-x: auto;
  overflow-y: hidden;
  display: block;
}

/*
 * Links Default
 */
a{
  text-decoration: none;
}
/*
 * Bold Text
 */
b, strong{
  font-weight:bold;
}
/*
 * Cursive
 */
em{
  font-style: italic;
}